* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


#root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.AppHeader {
    width: 100vw;
}

.AppSection {
    width: 800px;
}


.post {
    display: flex;
    border: 2px solid teal;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin-top: 15px;
    font-size: 25px;

}

.post_btns {
    display: flex;
}

.post_btns>button:first-child {
    margin-right: 10px;
}

.post-enter {
    opacity: 0.8;
    transform: translateX(-450px);
}

.post-enter-active {
    opacity: 1;
    transition: all 500ms ease-in;
    transform: translateX(0);
}

.post-exit {
    opacity: 1;
}

.post-exit-active {
    opacity: 0;
    transition: all 500ms ease-in;
    transform: translateX(-450px);
}

.page__wrapper {
    display: flex;
    margin-block: 15px;
    user-select: none;
    cursor: pointer;
}

.page {
    border: 1px solid teal;
    padding-inline: 15px;
    padding-block: 10px;
}

.page__current {
    border: 2px solid teal;
    transform: scale(1.1);
    background: teal;
    font-weight: bold;
}

.navbar {
    display: flex;
    width: 100%;
    height: 50px;
    justify-content: space-evenly;
    align-items: center;
    background: lightgray;
    user-select: none;
}

.navbar>button {
    margin-left: 15px;
}



.navbar__links {
    font-size: 18px;
    color: teal;
    font-weight: bold;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 10%;
}

.navbar__links>a {
    height: 100%;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}




.navbar__links>a:hover {
    text-decoration: underline;
    cursor: pointer;
    background: yellow;
}

.error__message {
    margin-top: 30%;
    color: red;
}