.loader {
    left: 250px;
    position: relative;
    width: 250px;
    height: 250px;
}


.circleFirst,
.circleSecond,
.circleThird {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.circleFirst {
    border-top: 3px solid #ff3e3e;
    animation: RotateOne linear 1.2s infinite;
}

.circleSecond {
    border-left: 3px solid #0095ff;
    animation: RotateTwo linear 1.2s infinite;
}

.circleThird {
    border-right: 3px solid #ffe743;
    animation: RotateThree linear 1.2s infinite;
}




@keyframes RotateOne {
    0% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(0);
    }
    100% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
    }
}

@keyframes RotateTwo {
    0% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(0);
    }
    100% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
    }
}
@keyframes RotateThree {
    0% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(0);
    }
    100% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
    }
}

