.myModal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: none;
    background-color: rgba(0, 0, 0, 0.4);
    user-select: none;
}

.myModalTurnOn {
    display: flex;
    justify-content: center;
    align-items: center;
}

.myModalContent {
    max-width: 400px;
    border-radius: 16px;
    padding: 25px;
    background: white;
}